import { Component, OnInit } from '@angular/core';
import { ModalController, NavController, LoadingController, AlertController } from '@ionic/angular';
import { SelProcessComponent } from '../sel-process/sel-process.component';
import { BaseService } from 'src/app/services/base.service';
import { Storage } from '@ionic/storage';
import { Constants, StatusCode } from 'src/app/data-model/constant.model';
import { ProcessModel, DeptModel, AccessTokenModel } from 'src/app/data-model/base.model';
import { BasePagePage } from 'src/app/shared/base-page/base-page.page';

@Component({
  selector: 'app-sel-dept',
  templateUrl: './sel-dept.component.html',
  styleUrls: ['./sel-dept.component.scss'],
})
export class SelDeptComponent extends BasePagePage implements OnInit {

  deptReqModel: DeptModel = new AccessTokenModel();
  arrDept: Array<any> = [];

  isSelectMachine: boolean = false;
  isIndeterminate: boolean;
  masterCheck: boolean;
  checkBoxList: any;

  constructor(
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    private modalCtrl: ModalController,
    public navCtrl: NavController,
    private service: BaseService,
    private storage: Storage) {

    super(loadingCtrl, alertCtrl);

  }

  ngOnInit() {

    this.storage.remove(Constants.c_OP_MACHINE_LIST);

    this.callWSGetDept();

  }

  // ionViewWillEnter () {

  //   this.callWSGetDept();

  // }

  async cancelModal() {
    // const onClosedData: string = "Wrapped Up!";
    await this.modalCtrl.dismiss();
  }

  async closeModal() {
    // const onClosedData: string = "Wrapped Up!";
    // await this.modalCtrl.dismiss(this.checkBoxList);
    this.navigateToSelProcess();
    // this.navCtrl.navigateForward('/sel-process');
  }

  callWSGetDept() {

    this.storage.get(Constants.c_ACCESS_TOKEN).then((accessToken) => {

      this.deptReqModel.accesstoken = accessToken;

      this.presentLoading();

      this.service.callWebService(Constants.k_GET_DEPT, this.deptReqModel, (res) => {

        if (res.status_code == StatusCode.Success) {

          this.arrDept = JSON.parse(res.data);

          this.checkBoxList = [];

          this.storage.get(Constants.c_OP_SEL_DEPT).then((department) => {

            if (department == undefined || department == null) {

              this.arrDept.forEach(element => {

                this.checkBoxList.push({
                  'dept': element,
                  'isChecked': false
                })

              });

            } else {

              let arrSelectedDept: Array<any> = department;

              this.arrDept.forEach(element => {

                this.checkBoxList.push({
                  'dept': element,
                  'isChecked': arrSelectedDept.includes(element.value)
                })

              });

            }

            this.checkEvent();

            this.dismissLoading();

          });

        } else {

          this.dismissLoading();

        }

      });

    });

  }

  checkMaster() {
    setTimeout(() => {
      this.checkBoxList.forEach(obj => {
        obj.isChecked = this.masterCheck;
      });
    });
  }

  checkEvent() {
    const totalItems = this.checkBoxList.length;
    let checked = 0;
    this.checkBoxList.map(obj => {
      if (obj.isChecked) checked++;
    });
    if (checked > 0 && checked < totalItems) {
      //If even one item is checked but not all
      this.isIndeterminate = true;
      this.masterCheck = false;
    } else if (checked == totalItems) {
      //If all are checked
      this.masterCheck = true;
      this.isIndeterminate = false;
    } else {
      //If none is checked
      this.isIndeterminate = false;
      this.masterCheck = false;
    }
  }

  navigateToSelProcess() {

    var arrSelectedDept: Array<any> = [];

    this.checkBoxList.forEach(element => {
      if (element.isChecked) {
        arrSelectedDept.push(element.dept.value)
      }
    });

    // CACHE FOR DEPT SELECTION IF USER ENTER FOR SECOND TIME AFTER CLOSE / KILL APP
    this.storage.set(Constants.c_OP_SEL_DEPT, arrSelectedDept)

    if (this.disableSelectButton()) {

      this.service.presentAlert('Alert', 'Please select at least one (1) department.');

    } else {

      const nav = document.querySelector('ion-nav');

      nav.push(SelProcessComponent, {

        "arrSelectedDept": arrSelectedDept,

      });

    }

  }

  disableSelectButton(): boolean {
    var isCheckedAtLeastOne: boolean = false;
    this.checkBoxList.forEach(element => {
      if (element.isChecked) {
        isCheckedAtLeastOne = true
        return;
      }
    });
    return !isCheckedAtLeastOne
  }

  onClose() {
    this.modalCtrl.dismiss();
  }

}
