import { Component, OnInit } from '@angular/core';
import { BasePagePage } from 'src/app/shared/base-page/base-page.page';
import { LoadingController, ModalController, NavController, NavParams, AlertController } from '@ionic/angular';
import { Storage } from '@ionic/storage';
import { Constants, StatusCode } from 'src/app/data-model/constant.model';
import { MachineModel } from 'src/app/data-model/base.model';
import { BaseService } from 'src/app/services/base.service';

@Component({
  selector: 'app-sel-machine',
  templateUrl: './sel-machine.component.html',
  styleUrls: ['./sel-machine.component.scss'],
})
export class SelMachineComponent extends BasePagePage implements OnInit {

  arrSelectedProcess: Array<any> = [];
  arrMachine: Array<any> = [];
  machineReqModel: MachineModel = new MachineModel();
  machine: any;

  constructor(
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    private modalCtrl: ModalController,
    public navCtrl: NavController,
    private navParams: NavParams,
    private service: BaseService,
    private storage: Storage) {

    super(loadingCtrl, alertCtrl);

  }

  ngOnInit() {
    this.arrSelectedProcess = this.navParams.data.arrSelectedProcess;
    if (!(this.arrSelectedProcess == null || this.arrSelectedProcess == undefined || this.arrSelectedProcess.length == 0)) {
      this.callWSGetMachine();
    }
  }

  async cancelModal() {
    // const onClosedData: string = "Wrapped Up!";
    await this.modalCtrl.dismiss();
  }

  async closeModal() {
    // const onClosedData: string = "Wrapped Up!";
    await this.modalCtrl.dismiss(this.machine);
  }

  callWSGetMachine() {

    this.machineReqModel.process = this.arrSelectedProcess;

    this.storage.get(Constants.c_OP_MACHINE_LIST).then((arrMachineList) => {

      if (arrMachineList == null || arrMachineList.length == 0) {

        this.presentLoading();
        
        this.storage.get(Constants.c_ACCESS_TOKEN).then((accessToken) => {

          this.machineReqModel.accesstoken = accessToken;
    
          this.service.callWebService(Constants.k_GET_MACHINE, this.machineReqModel, (res) => {
    
            if (res.status_code == StatusCode.Success) {
    
              this.arrMachine = JSON.parse(res.data);
    
              this.storage.set(Constants.c_OP_MACHINE_LIST, this.arrMachine);
    
            }
    
            this.dismissLoading();
    
          });
    
        });

      } else {

        this.arrMachine = arrMachineList;

        this.dismissLoading();
        
      }

    });

  }

  onMachineClicked(machine: any) {
    this.machine = machine;

    // CACHE FOR MACHINE SELECTION IF USER ENTER FOR SECOND TIME AFTER CLOSE / KILL APP
    this.storage.set(Constants.c_OP_SEL_MACH, this.machine)

    this.closeModal();
  }

}
