import { Component, OnInit } from '@angular/core';
import { ModalController, NavParams, NavController, LoadingController, AlertController } from '@ionic/angular';
import { SelMachineComponent } from '../sel-machine/sel-machine.component';
import { BaseService } from 'src/app/services/base.service';
import { Storage } from '@ionic/storage';
import { Constants, StatusCode } from 'src/app/data-model/constant.model';
import { MachineModel, ProcessModel } from 'src/app/data-model/base.model';
import { BasePagePage } from 'src/app/shared/base-page/base-page.page';

@Component({
  selector: 'app-sel-process',
  templateUrl: './sel-process.component.html',
  styleUrls: ['./sel-process.component.scss'],
})
export class SelProcessComponent extends BasePagePage implements OnInit {

  isSelectMachine: boolean = false;
  isIndeterminate: boolean;
  masterCheck: boolean;
  checkBoxList: any;

  arrSelectedDept: Array<any> = [];
  arrProcess: Array<any> = [];
  processReqModel: ProcessModel = new ProcessModel();

  constructor(
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    private modalCtrl: ModalController,
    public navCtrl: NavController,
    private navParams: NavParams,
    private service: BaseService,
    private storage: Storage) {

    super(loadingCtrl, alertCtrl);

  }

  ngOnInit() {
    this.arrSelectedDept = this.navParams.data.arrSelectedDept;
    this.callWSGetProcess();
  }

  checkMaster() {
    setTimeout(() => {
      this.checkBoxList.forEach(obj => {
        obj.isChecked = this.masterCheck;
      });
    });
  }

  checkEvent() {
    const totalItems = this.checkBoxList.length;
    let checked = 0;
    this.checkBoxList.map(obj => {
      if (obj.isChecked) checked++;
    });
    if (checked > 0 && checked < totalItems) {
      //If even one item is checked but not all
      this.isIndeterminate = true;
      this.masterCheck = false;
    } else if (checked == totalItems) {
      //If all are checked
      this.masterCheck = true;
      this.isIndeterminate = false;
    } else {
      //If none is checked
      this.isIndeterminate = false;
      this.masterCheck = false;
    }
  }

  onClose() {
    this.modalCtrl.dismiss();
  }

  async cancelModal() {
    // const onClosedData: string = "Wrapped Up!";
    await this.modalCtrl.dismiss();
  }

  async closeModal() {
    // const onClosedData: string = "Wrapped Up!";
    await this.modalCtrl.dismiss(this.checkBoxList);
  }

  callWSGetProcess() {

    this.processReqModel.dept = this.arrSelectedDept;

    // if (this.disableSelectButton()) {

    //   this.service.presentAlert('Alert', 'Please select at least one (1) department.');

    // } else {

    // }

    this.storage.get(Constants.c_ACCESS_TOKEN).then((accessToken) => {

      this.processReqModel.accesstoken = accessToken;

      this.presentLoading();

      this.service.callWebService(Constants.k_GET_PROCESS, this.processReqModel, (res) => {

        this.arrProcess = JSON.parse(res.data);

        this.checkBoxList = [];

        this.storage.get(Constants.c_OP_SEL_PROC).then((process) => {

          if (process == undefined || process == null) {

            this.arrProcess.forEach(element => {

              this.checkBoxList.push({
                'process': element,
                'isChecked': false
              })

            });

          } else {

            let arrSelectedProc: Array<any> = process;

            this.arrProcess.forEach(element => {

              this.checkBoxList.push({
                'process': element,
                'isChecked': arrSelectedProc.includes(element.value)
              })

            });

          }

          this.checkEvent();

          this.dismissLoading();

        });

      });

    });

  }

  navigateToSelMachine() {

    var arrSelectedProcess: Array<any> = [];

    this.checkBoxList.forEach(element => {
      if (element.isChecked) {
        arrSelectedProcess.push(element.process.value)
      }
    });

    // CACHE FOR PROCESS SELECTION IF USER ENTER FOR SECOND TIME AFTER CLOSE / KILL APP
    this.storage.set(Constants.c_OP_SEL_PROC, arrSelectedProcess)

    if (this.disableSelectButton()) {

      this.service.presentAlert('Alert', 'Please select at least one (1) process.');

    } else {

      const nav = document.querySelector('ion-nav');

      nav.push(SelMachineComponent, {

        "arrSelectedProcess": arrSelectedProcess,

      });

    }

  }

  disableSelectButton(): boolean {
    var isCheckedAtLeastOne: boolean = false;
    this.checkBoxList.forEach(element => {
      if (element.isChecked) {
        isCheckedAtLeastOne = true
        return;
      }
    });
    return !isCheckedAtLeastOne
  }

}
